:root {
  --site-width: 100%;
  --dialog-width: 70%;
  --dialog-large-width: 90%;
  --header-height: 45px;
  --header-margin-top: 0px;
  --header-margin-bottom: 0px;
  --footer-height: 50px;
  --footer-margin-top: 0px;
  --footer-margin-bottom: 0px;
  --footer-credits-margin-top: 13px;
  --nav-height: 24;
  --main-height: 119px;
}

header,
footer,
main,
nav {
  width: var(--site-width);
}

header {
  height: auto;
}
header span.site-name {
  font-size: 24rem;
  border-bottom: 0px;
  margin-left: auto;
  margin-right: auto;
}

nav {
  padding: 3px 0px;
}
nav span.menu-button {
  display: block;
}
nav > ul {
  display: none;
  width: 100%;
}
nav > ul > li {
  display: block;
}
nav > ul > li div.sub-menu {
  position: relative;
}
nav > ul > li div.sub-menu > ul > li a {
  width: 100%;
}

main > article::after {
  content: "< 576px micro";
  display: block;
  margin: 10px;
  padding: 10px;
}

@media (min-width: 576px) {
  main > article::after {
    content: "576px";
  }

  :root {
    --site-width: 540px;
    --dialog-width: 270px;
    --dialog-large-width: 405px;
    --header-height: 70px;
    --header-margin-top: 20px;
