    --footer-height: 50px;
    --footer-margin-top: 15px;
    --footer-margin-bottom: 10px;
    --footer-credits-margin-top: 13px;
    --nav-height: 24;
    --main-height: 189px;
  }

  header,
footer,
main,
nav {
    width: var(--site-width);
  }

  header {
    height: var(--header-height);
  }
  header span.site-name {
    font-size: 16rem;
    border-bottom: var(--text-heading-border) solid 7px;
    margin: 12px 8px;
  }

  nav {
    padding: 0px;
  }
  nav span.menu-button {
    display: none;
  }
  nav > ul {
    display: table !important;
    width: inherit;
  }
  nav > ul > li {
    display: inline-block;
  }
  nav > ul > li div.sub-menu {
    position: absolute;
  }
}
@media (min-width: 768px) {
  main > article::after {
    content: "768px";
  }

  :root {
    --site-width: 720px;
    --dialog-width: 360px;
    --dialog-large-width: 540px;
  }

  header,
footer,
main,
nav {
    width: var(--site-width);
  }
}
@media (min-width: 992px) {
  main > article::after {
    content: "992px";
  }

  :root {
    --site-width: 900px;
    --dialog-width: 450px;
    --dialog-large-width: 675px;
  }

  header,
footer,
main,
nav {
    width: var(--site-width);
  }
}
@media (min-width: 1200px) {
  main > article::after {
